@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  @font-face {
    font-family: "Buffon";
    src: local("Buffon"), url(./fonts/Buffon-Regular.otf) format("opentype");
  }

  @font-face {
    font-family: "Buffon";
    font-weight: 200;
    src: local("Buffon"), url(./fonts/Buffon-Thin.otf) format("opentype");
  }

  @font-face {
    font-family: "Buffon";
    font-weight: 600;
    src: local("Buffon"), url(./fonts/Buffon-Bold.otf) format("opentype");
  }

  @font-face {
    font-family: "Buffon";
    font-weight: 900;
    src: local("Buffon"), url(./fonts/Buffon-Heavy.otf) format("opentype");
  }
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

h1 {
  font-size: 3em;
}
h2 {
  font-size: 2em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
